// src/components/Header/Header.tsx

import React from 'react';
import { NavLink } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Container, makeStyles } from '@material-ui/core';
import { headerStyles } from '../styles/headerStyles';

const useStyles = makeStyles((theme) => ({
    toolbar: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    navLinks: {
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center',
        textDecoration: 'none',
        listStyle: 'none',
        width: '50%',
    },
    navLink: {
        color: theme.palette.common.white,
        textDecoration: 'none',
        '&:hover': {
            textDecoration: 'underline',
        },
    },
    active: {
        textDecoration: 'underline',
    },
}));

const Header: React.FC = () => {
    const classes = useStyles();
    const headerClasses = headerStyles();

    return (
        <AppBar position="static" className={headerClasses.header}>
            <Container maxWidth="md">
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h6">
                        Cheng Dai
                    </Typography>
                    <nav className={classes.navLinks}>
                    {/*<NavLink to="/"  className={classes.navLink}>About me</NavLink>*/}
                    <NavLink to="/school-life"  className={classes.navLink}>CV</NavLink>
                    {/*<NavLink to="/working-experience" activeClassName={classes.active} className={classes.navLink}>Working Experience</NavLink>*/}
                    {/*<NavLink to="/project-experience" activeClassName={classes.active} className={classes.navLink}>Project Experience</NavLink>*/}
                    {/*<NavLink to="/cv" activeClassName={classes.active} className={classes.navLink}>CV</NavLink>*/}
                    </nav>
                </Toolbar>
            </Container>
        </AppBar>
    );
};


export default Header;

